import $ from 'ZEPTO_PATH';
import actGoodsSlider from 'MODULES_PATH/actGoodsSlider';
import addMtaAttr from 'MODULES_PATH/addMtaAttr';
import VARIABLE from 'UNIT_PATH/variable';
import updateSlider from 'UNIT_PATH/updateSlider';

var model = require('UNIT_PATH/model');
var view = require('UNIT_PATH/view');

function main() {
	var item = $('.red-actlist .act-goods-list:not(.guess-like-goods) li[data-goods-id]');
	var priceAndStockRequest = 0;
	var skuidsv = [];
	var slider;

	item.each(function(i, v) {
		skuidsv.push($(this).attr('data-goods-id').split('_')[1]);
	});

	// slider = actGoodsSlider($('.red-actlist .act-goods-list:not(.guess-like-goods) .swiper-container'));

	// 
	model.getPriceData(skuidsv.join(','), function(data) {
		$('.act-goods-list:not(.guess-like-goods)').each(function(i, v) {
			view.renderActPrice(data, skuidsv, $(this));
		});
    	
    	VARIABLE.GLOBAL.SCROLL_OBJ && VARIABLE.GLOBAL.SCROLL_OBJ.refresh();
    	priceAndStockRequest++;
    	if(priceAndStockRequest == 2) {
    		setTimeout(function() {
				$('.act-goods-list:not(.guess-like-goods)').each(function(i, v) {
					clearProduct($(this));
				});
				slider = actGoodsSlider($('.red-actlist .act-goods-list:not(.guess-like-goods) .swiper-container'));
				VARIABLE.GLOBAL.SCROLL_OBJ && VARIABLE.GLOBAL.SCROLL_OBJ.refresh();
			}, 800);
    	}
    });

	// 
    model.getStockData(skuidsv.join(','), function(data) {
    	$('.act-goods-list:not(.guess-like-goods)').each(function(i, v) {
			view.renderStock(data, $(this));
		});
		
		VARIABLE.GLOBAL.SCROLL_OBJ && VARIABLE.GLOBAL.SCROLL_OBJ.refresh();
		priceAndStockRequest++;
		if(priceAndStockRequest == 2) {
			setTimeout(function() {
				$('.act-goods-list:not(.guess-like-goods)').each(function(i, v) {
					clearProduct($(this));
				});
				slider = actGoodsSlider($('.red-actlist .act-goods-list:not(.guess-like-goods) .swiper-container'));
				VARIABLE.GLOBAL.SCROLL_OBJ && VARIABLE.GLOBAL.SCROLL_OBJ.refresh();
			}, 800);
			
    	}
	});
}

// 
function clearProduct(item) {
	var thisLi = item.find('li[data-goods-id]');

	addMtaAttr(item);
	if(thisLi.length > 8) {
		for(var i = thisLi.length; i > 8; i--) {
			thisLi.eq(i - 1).remove();
		}
	}
}

module.exports = main;


