window.$ = $;

// 
//layout();

// 
header.headerInit();

// 
Model.getQdStatus(function(data) {
	View.renderQdStatus(data);
	eventBind();
});

function eventBind() {
	$('#set-btn').bind('click', function() {
		if($(this).hasClass('qdcomplete')) {
			if(VARIABLE.CONFIG.VS == 'jdapp') {
				jdshare.callSharePane({
	                title: '',
	                content: '',
	                url: 'http://h5.m.jd.com/active/rushbuying/index.html',
	                img: 'http://m.360buyimg.com/n1/s120x120_jfs/t2566/341/1119128176/23675/6356333b/568e3d86Naa36a750.jpg',
	                channel: '',
	                callback: null
	            });
			};
			return;
		};

		if($(this).attr('data-islogin') != 1) {
			location.href = "//plogin.m.jd.com/user/login.action?appid=100&returnurl=" + encodeURIComponent(window.location.href);
			return;
		}

		Model.setQdStatus(function(data) {
			View.renderQdStatus(data);
		});
	});
}




