import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import UTILS from 'UTILS_PATH/helper';

class NewYear{
	constructor(opts) {
		this.count = opts.imgCount;
		this.urlArr = opts.imgUrl;
		this.box = opts.box;
		this.imgDataArr = [];
		this.size = 120;
		this.time = opts.time || 20;
		this.disTime = opts.disTime || 300;
	}

	init() {
		var _winHeight = $(window).height();
		var _winWidth = $(this.box).width() - 10;
		var rootSize = parseInt($('html').css('fontSize'), 10);

		for(var i = 0; i < this.count; i++) {
			var imgData = {};

			var _x = UTILS.rnd(10, _winWidth);

			imgData.size = this.size;
			imgData.index = imgData.size + 1000;
			imgData.url = this.urlArr[UTILS.rnd(0, this.urlArr.length)];
			imgData.time = 1 + UTILS.rnd(1, this.time) / 10;

			if(_x + imgData.size / 100 * rootSize > _winWidth) {
				_x = _winWidth - imgData.size / 100 * rootSize;
			}

			imgData.trajectory = {
				start: {
					x: _x,
					y: -imgData.size
				},

				end: {
					x: _x,
					y: _winHeight
				}
			}
			this.imgDataArr.push(imgData);
			var _style = 'pointer-events: none; position: absolute; left: 0px; top: 0; width: ' + imgData.size / 100 + 'rem; height: ' + imgData.size / 100 + 'rem; z-index:' + imgData.index + '; background: url('+ imgData.url +') no-repeat left top; background-size: 100% 100%;';
			$(this.box).append('<div class="img-anima" style="'+ _style +'"></div>');
		}

		this.setStart();

	}

	setStart() {
		var _this = this;

		$('.img-anima').each(function(i, v) {
			var _thisImg = _this.imgDataArr;
			var startPoint = _thisImg[i].trajectory.start;
			var endPoint = _thisImg[i].trajectory.end;
			var _that = this;

			UTILS.setTransformStyle(this, startPoint);
			UTILS.setTransitionStyle(this, _thisImg[i].time + 's', 'linear');
		});
	}

	run() {
		var _this = this;
		var _len = $('.img-anima').length;
		var imgs = $('.img-anima');
		var _index = 0;

		var timer = setInterval(function() {
			var _thisImg = _this.imgDataArr;
			var endPoint = _thisImg[_index].trajectory.end;

			UTILS.setTransformStyle(imgs.get(_index), endPoint);
			_index++;
			if(_index > _len - 1) {
				clearInterval(timer);
				timer = null;
			}
		}, this.disTime);

		$('.img-anima').bind('webkitTransitionEnd', function() {
			$(this).remove();
		});
	}
}

module.exports = NewYear;


