import Model from 'UNIT_PATH/model';
import View from 'UNIT_PATH/view';
import VARIABLE from 'UNIT_PATH/variable'; 
import util from 'UTILS_PATH/helper';
import actGoodsSlider from 'MODULES_PATH/actGoodsSlider';
import updateSlider from 'UNIT_PATH/updateSlider';
import LoadTpl from 'UNIT_PATH/LoadTpl';

let slider;

const addMapingAttr = (actItem, index) => {
	let allLi = actItem.find('li:not(.total)');
    let actImg = actItem.prev('a[report-eventid="MFlashbuy_DIYZone"]');

    if(actImg.length) {
        actImg.attr('report-eventparam', 'todaynewest_'+ (5 + index * 9) +'_guessulikeact');
    }

	allLi.each(function(i, v) {
		let reportEventparam = 'todaynewest_' + (6 + i + index * 9) +'_guessulikesku' + (i + 1);
		
		$(this).find('a').addClass('J_ping').attr({
    		'report-eventid': 'MFlashbuy_DIYZone',
    		'report-eventparam': reportEventparam
    	});
	});
}

const clearList = (actItem, index) => {
	let allLi = actItem.find('li:not(.total)');

	for(let i = 0; i < allLi.length; i++) {
		if(i > 7) {
			allLi.eq(i).remove();
			i--;
			allLi = actItem.find('li:not(.total)');
		}
	}

	addMapingAttr(actItem, index);
}

const showMaping = () => {
	let goodsList = '<div class="guess-like-goods act-goods-list border-bottom dbcdadada" data-xhr-layzr="true">'+
                '<div class="swiper-container">' +
                    '<ul class="swiper-wrapper">' +
                        '<li class="swiper-slide">' +
                            '<a href="javascript:;" class="J_ping" report-eventid="MFlashbuy_DIYZone" report-eventparam="todaynewest_6_guessulikesku1">' +
                                '<div class="goods-img default-type2">' +
                                '</div>' +
                            '</a>' +
                        '</li>' +
                        '<li class="swiper-slide">' +
                            '<a href="javascript:;" class="J_ping" report-eventid="MFlashbuy_DIYZone" report-eventparam="todaynewest_7_guessulikesku2">' +
                                '<div class="goods-img default-type2">' +
                                '</div>' +
                            '</a>' +
                        '</li>' +
                        '<li class="swiper-slide">' +
                            '<a href="javascript:;" class="J_ping" report-eventid="MFlashbuy_DIYZone" report-eventparam="todaynewest_8_guessulikesku3">' +
                                '<div class="goods-img default-type2">' +
                                '</div>' +
                            '</a>' +
                        '</li>' +
                        '<li class="swiper-slide">' +
                            '<a href="javascript:;" class="J_ping" report-eventid="MFlashbuy_DIYZone" report-eventparam="todaynewest_9_guessulikesku4">' +
                                '<div class="goods-img default-type2">' +
                                '</div>' +
                            '</a>' +
                        '</li>' +
                        '<li class="swiper-slide">' +
                            '<a href="javascript:;" class="J_ping" report-eventid="MFlashbuy_DIYZone" report-eventparam="todaynewest_10_guessulikesku5">' +
                                '<div class="goods-img default-type2">' +
                                '</div>' +
                            '</a>' +
                        '</li>' +
                        '<li class="swiper-slide">' +
                            '<a href="javascript:;" class="J_ping" report-eventid="MFlashbuy_DIYZone" report-eventparam="todaynewest_11_guessulikesku6">' +
                                '<div class="goods-img default-type2">' +
                                '</div>' +
                            '</a>' +
                        '</li>' +
                        '<li class="swiper-slide">' +
                            '<a href="javascript:;" class="J_ping" report-eventid="MFlashbuy_DIYZone" report-eventparam="todaynewest_12_guessulikesku7">' +
                                '<div class="goods-img default-type2">' +
                                '</div>' +
                            '</a>' +
                        '</li>' +
                        '<li class="swiper-slide">' +
                            '<a href="javascript:;" class="J_ping" report-eventid="MFlashbuy_DIYZone" report-eventparam="todaynewest_13_guessulikesku8">' +
                                '<div class="goods-img default-type2">' +
                                '</div>' +
                            '</a>' +
                        '</li>' +
                    '</ul>' +
                '</div>' +
            '</div>';

    $('.item').eq(0).before(goodsList);

	let actHtml = '<a href="javascript:;" class="showgoods-item item J_ping" report-eventid="MFlashbuy_DIYZone" report-eventparam="todaynewest_5_guessulikeact">' +
                '<div class="img-wraper default-type3">'+
                '</div>'+
            '</a>';

	$('.guess-like-goods').eq(0).before(actHtml);
	actGoodsSlider($('.guess-like-goods .swiper-container'));
}

const replaceEventId = () => {
    if(util.queryUrl(window.location.href).from_react) {
        $('.red-page *[report-eventid]').each(function() {
            var oldVal = $(this).attr('report-eventid');
            var newVal = oldVal.replace(/^M/, '');

            $(this).attr('report-eventid', newVal);
        });
    }
}

const getPrice = () => {
	let sku = [];
	let skuLi = $('.guess-like-goods li:not(.total)');

	if(!skuLi.length) return; 

	skuLi.each(function(i, v) {
		sku.push($(this).attr('data-goods-id'));
	});

	// 
	Model.getPriceData(sku.join(','), (data) => {
		$('.guess-like-goods').each(function(i, v) {
			View.renderActGuessLikePrice(data, $(this), $(this).find('li').eq(0).attr('data-goods-id'));
		});

		Model.getStockData(sku.join(','), function(data) {
			$('.guess-like-goods').each(function(i, v) {
				View.renderGuessStock(data, $(this), $(this).find('li').eq(0).attr('data-goods-id'));
				clearList($(this), i);
			});
			
			slider = actGoodsSlider($('.guess-like-goods .swiper-container'));
		});
	});
}

const main = () => {
	let href = window.location.href;
	let recommParam = util.queryUrl(href).recommParam;

	if(util.queryUrl(window.location.href).usescroll) {
		showMaping();
        replaceEventId();
        return;
	}

	if(VARIABLE.CONFIG.VS == 'weixin' || $('body').attr('data-showYouLike') != 1) return;
	if(recommParam) {
        Model.getIndexGuessLike((data) => {
            data.reverse();
            for(var i = 0; i < data.length; i++) {
                View.renderActGuessLikeGoods(data[i], LoadTpl.loadActGuessLikeSkuListTpl());
                View.renderActGuessLike(data[i], i, LoadTpl.loadActGuessLikeTpl());
                $('.item[actid="'+ data[i].actId +'"]').remove();
                $('.act-goods-list[actid="'+ data[i].actId +'"]').remove();
            };

            getPrice();
            VARIABLE.GLOBAL.SCROLL_OBJ && VARIABLE.GLOBAL.SCROLL_OBJ.refresh();
        });
	}
}

export default main;


