import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import Loading from 'UNIT_PATH/loading';
var util = require('UTILS_PATH/helper');
const CDN = '//static.360buyimg.com/shangou/redm/m-new/public/dist/' + VARIABLE.CONFIG.VERSION;

class LoadTpl{
	// 
	static loadActGuessLikeTpl(callback) {
		return '<a href="{{=it.actHref}}" class="showgoods-item item J_ping" report-eventid="MFlashbuy_DIYZone">'+
			        '<div class="img-wraper default-type3">'+
			            '<img src="{{=it.coverImg2}}" onerror="imgError(this)">'+
			        '</div>'+
			        '<div class="act-detail">'+
			            '<div class="brand-logo">'+
			                '<img src="{{=it.whiteLogoImg}}" onerror="imgError(this)">'+
			            '</div>'+
			            '<p>{{=it.title}}</p>'+
			            '{{? it.reduceMsg != "" }}'+
			            	'<span class="preferential-msg">{{=it.reduceMsg}}</span>'+
			            '{{?}}'+
			        '</div>'+
			        '{{? it.colorCard618Visible }}'+
			        	'<span class="icon-618"></span>'+
			        '{{?}}'+
			    '</a>';
	}

	// 
	static loadActGuessLikeSkuListTpl(callback) {
		return '<div class="guess-like-goods act-goods-list border-bottom dbcdadada" data-skus="{{=it.skus}}">'+
			        '<div class="swiper-container">'+
			            '<ul class="swiper-wrapper">'+
			                '{{ for(var prop in it.goods) { }}'+
			                    '<li class="swiper-slide {{=it.goods[prop].className}}" data-goods-id="{{=it.goods[prop].skuid}}">'+
			                        '<a href="{{=it.goods[prop].href}}">'+
			                            '<div class="goods-img default-type2">'+
			                                '<img src="{{=it.goods[prop].imgurl}}" loaded="true" onerror="imgError(this)">'+
			                            '</div>'+
			                            '<p class="goods-price">'+
			                                '<span class="n-price"><em></em></span>'+
			                                '<del class="o-price"><em></em></del>'+
			                            '</p>'+
			                        '</a>'+
			                    '</li>'+
			                '{{ } }}'+
							'<li class="swiper-slide total">'+
								'<a href="//redact.m.jd.com/detail/{{=it.actId}}.html">'+
			                        '<div class="total-count">'+
			                            '<div class="total-inner">'+
			                                '<p class="txt"></p>'+
			                                '<p class="num">{{=it.totalGoodsNum}}</p>'+
			                            '</div>'+
			                        '</div>'+
								'</a>'+
			                '</li>'+
			            '</ul>'+
			        '</div>'+
			    '</div>';
	}
}

export default LoadTpl;


