import $ from 'ZEPTO_PATH';
import NewYear from 'MODULES_PATH/newYearAnimation';
var cookie = require('UTILS_PATH/cookie');

function main() {
	var imgs = $('body').attr('data-imgs');
	var lastImgs = cookie('lastimgs');
	var date = new Date();
	var disTime = parseInt($('body').attr('data-distime'), 10);
	var time = (parseInt($('body').attr('data-time'), 10) - 1) * 10 ;

	date.setDate(date.getDate() + 1);
	date.setHours(0);
	date.setMinutes(0);
	date.setSeconds(0);

	if(imgs && lastImgs != imgs) {
		var newYearAnimation = new NewYear({
			imgCount: 20,
			imgUrl: imgs.split(','),
			box: '.red-page',
			disTime: disTime,
			time: time
		});

		newYearAnimation.init();
		newYearAnimation.run();
		cookie('lastimgs', imgs, {expires: date});
	}
}

module.exports = main;


