import $ from 'ZEPTO_PATH';
import VARIABLE from 'UNIT_PATH/variable';
import flexibleArea from 'MODULES_PATH/flexibleArea';
import Model from 'UNIT_PATH/model';
import View from 'UNIT_PATH/view';
import util from 'UTILS_PATH/helper';

var main = () => {
	Model.getNewUserEnter((data) => {
		var hasContent = View.renderNewUserEnter(data);

		if(hasContent) scale();
	});
}

var scale = () => {
	$('#goodsForNewUserHtml').removeClass('none');
	flexibleArea($('#goodsForNewUserHtml .flexible-area-inner'), function(_this) {
		_this.removeClass('op0').removeClass('none');
		_this.closest('.flexible-area-con').find('.load-wrap').remove();
		VARIABLE.GLOBAL.SCROLL_OBJ && VARIABLE.GLOBAL.SCROLL_OBJ.refresh();
	});
}

export default main;


