import Model from 'UNIT_PATH/model';
import View from 'UNIT_PATH/view';
import VARIABLE from 'UNIT_PATH/variable'; 
import util from 'UTILS_PATH/helper';

const clearList = () => {
	let allLi = $('#guess-like li');

	// sku
	for(let i = 0; i < allLi.length; i++) {
		if(i > 0) {
			if(allLi.eq(i).hasClass('error-price') && !allLi.eq(i).hasClass('is-white') && allLi.length > 2) {
				allLi.eq(i).remove();
				i--;
			}
		}

		allLi = $('#guess-like li');
	}

	let count = 0;

	// sku
	for(let i = 0; i < allLi.length; i++) {
		if(i > 0) {
			if(!allLi.eq(i).hasClass('error-price') && allLi.eq(i).hasClass('is-white')) {
				count++;
			}
		}
	}

	if(count >= 1) {
		for(let i = 0; i < allLi.length; i++) {
			if(i > 0) {
				if(allLi.eq(i).hasClass('error-price') || !allLi.eq(i).hasClass('is-white')) {
					allLi.eq(i).remove();
					i--;
				}
			}

			allLi = $('#guess-like li');
		}
	}else{
		// sku
		for(let i = 0; i < allLi.length; i++) {
			if(i > 0) {
				if(allLi.eq(i).hasClass('error-price') && allLi.length > 2) {
					allLi.eq(i).remove();
					i--;
				}
			}

			allLi = $('#guess-like li');
		}
	}

	for(let i = 0; i < allLi.length; i++) {
		if(i > 1) {
			allLi.eq(i).remove();
			i--;
		}

		allLi = $('#guess-like li');
	}

	$('.guess-right').removeClass('none');
}

const getPrice = () => {
	let sku = [];

	if(!$('.guess-right li').length) return; 

	$('.guess-right li').each(function(i, v) {
		sku.push($(this).attr('data-sku'))
	});

	Model.getPriceData(sku.join(','), (data) => {
		View.renderIndexGuessLikePrice(data);
		clearList();
	});
}

const showMaping = () => {
	let liWrap = $('#guess-like .guess-right');

	$('#guess-like').removeClass('none');
	$('.guess-left a').attr({
		'report-eventid': 'MFlashbuy_DIYZone',
		'report-eventparam': 'todaynewest_5_guessulikeact'
	}).addClass('J_ping');

	for(var i = 0; i < 2; i++) {
		var href = 'javascript:;';
		var mapAttr = '';

		if(i == 0) {
			mapAttr = 'class="J_ping" report-eventid="MFlashbuy_DIYZone" report-eventparam="todaynewest_6_guessulikesku1"';
		}else{
			mapAttr = 'class="J_ping" report-eventid="MFlashbuy_DIYZone" report-eventparam="todaynewest_7_guessulikesku2"';
		}
		
		liWrap.append('<li class="fl">'+
						'<a href="'+href+'" '+ mapAttr +' style="display: block; height: 1rem">'+
		                    
	                    '</a>'+
	                '</li>');
	}
}

const main = () => {
	let href = window.location.href;
	let recommParam = util.queryUrl(href).recommParam;

	if(util.queryUrl(window.location.href).usescroll) {
		return showMaping();
	}

	if(!$('#guess-like').length || VARIABLE.CONFIG.VS == 'weixin') return;
	if(recommParam) {
		Model.getIndexGuessLike((data) => {
			$('#guess-like').removeClass('none');
        	VARIABLE.GLOBAL.SCROLL_OBJ && VARIABLE.GLOBAL.SCROLL_OBJ.refresh();
			View.indexGuessLike(data);
			getPrice();
		});
	}
}

export default main;


