import $ from 'ZEPTO_PATH';
import Loading from 'UNIT_PATH/loading';
import Remind from 'UNIT_PATH/remind';

var VARIABLE = require('UNIT_PATH/variable'); 
var Model = require('UNIT_PATH/model');
var View = require('UNIT_PATH/view');

function main(item) {
    var allJpingA = item.find('.J_ping');
    var actId = item.attr('actid');
    var actItem = item.prev('a[report-eventid="MFlashbuy_DIYZone"]');

    if(actItem.length) {
        let allLi = item.find('li:not(.total)');
        let index = parseInt(actItem.attr('report-eventparam').split('_')[1], 10);

        allLi.each(function(i, v) {
            let reportEventparam = 'todaynewest_' + (index + i + 1) +'_guessulikesku' + (i + 1);
            
            $(this).find('a').addClass('J_ping').attr({
                'report-eventid': 'MFlashbuy_DIYZone',
                'report-eventparam': reportEventparam
            });
        });
    }else{
        allJpingA.each(function(i, v) {
            if($(this).find('.total-count').length) return;
            var li = $(this).closest('li');
            var sku = li.attr('data-goods-id').split('_')[1];

            $(this).attr({
                'report-eventparam': actId + '_' + sku + '_' + (i + 1),
                'report-eventid': 'MFlashbuy_BigProduct',
                'clstag': 'firsttype|keycount|shangoum|new_jrsx_bigbrandday_' + actId + '_' + sku + '_' + (i + 1)
            });
        });
    }
}

module.exports = main;


